/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

html, body {
	width: 100%;
	height: 100%;
	font-family: helvetica, arial, sans-serif;
}



header {
	position: relative;
	width: 100%;
	height: 100%;
	overflow: hidden;

	.title {
		position: absolute;
		top: 45%;
		left: 0;
		width: 100%;
		font-weight: bold;
		font-size: 3rem;
		text-align: center;

		span {
			display: inline-block;
		}
	}
}

video {
	position: fixed;
	top: 50%;
	left: 50%;
	min-width: 100%;
	min-height: 100%;
	transform: translate(-50%, -50%);
}


